import React  from "react";

const Todo = ({text}) => {
   return(
       <div class="ui relaxed divided list">
           <li className="todo-item">{text}</li>
           <button className="positive ui button" color='orange'>Check</button>
           <button className="negative ui button" color='red'>Trash</button>
       </div>
   );
}

export default Todo;