import React from "react";

// import components here
import Todo from "./Todo";

const TodoList = ({todos}) =>{
    return (
        <div className="ui container">
            <ui className="ui list">
                {todos.map(todo=>(
                    <Todo text={todo.text} key={todo.id}/>
                ))}
            </ui>
        </div>
    );
}

export default TodoList;