import React from "react";

const Form = ({setInputText,todos,setTodos,inputText}) =>{

    // Here I can write javascript code and function
    const inputTextHander = (e) => {
        console.log(e.target.value);
        setInputText(e.target.value);
    }

    // For Submit button handler
    const submitTodoHandler = (e) =>{
        e.preventDefault();
        
        setTodos([
            ...todos,{text:inputText,completed:false,id:Math.random()*1000}
        ]);
        setInputText("");
    }

    return (
        <div className="ui container">
            <form className="ui form">
                <div className="ui grid">
                    <div class="six wide column">
                        <input type="text" className="ui input" value={inputText} onChange={inputTextHander}/>
                    </div> 
                    <div className="three wide column">
                        <button type="submit" className="positive ui button" onClick={submitTodoHandler}>Submit</button>
                    </div>

                    <div class="right floated five wide column">
                        <select name="todos" className="ui select">
                            <option value="all">All</option>
                            <option value="completed">Completed</option>
                            <option value="uncompleted">Uncompleted</option>
                        </select>
                    </div>
                </div>
            </form>
        </div>
    );
}

export default Form;